@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

.bodyContainerSkills {
  padding-top: 30px;
  width: 100%;
  height: 100%;
}

.skills-container {
  background-color: #1b1b1b;
  color: #ffffff;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
  text-align: center;

}

.fromLeft {
  animation: 1s ease-out 0s 1 slideInFromLeft;
}

.fromRight {
  animation: 1s ease-out 0s 1 slideInFromRight;
}

h1 {
  margin-bottom: 20px;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.skill-item {
  margin: 10px;
  text-align: left;
  margin-bottom: 8px;
  width: 100%;
}

.skill-name {
  margin-bottom: 5px;
  font-weight: bold;
}

.skill-level {
  background-color: #333;
  height: 15px;
  border-radius: 8px;
  overflow: hidden;
}

.skill-fill {
  height: 100%;
  background-color: #4caf50;
  transition: width 1s ease-in-out;
}

.skills-category {
  background-color: black;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  width: 300px;
}

.skills-category:hover {
  transform: scale(1.02);
}

.iconContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
}

.roundIcon {
  border-radius: 5px;
}

.skillIcon {
  margin-top: auto;
  height: 40px;
  width: 40px;
}

.skills-category h2 {
  font-size: 2rem;
  margin-bottom: 10px;
  color: white;
}

.animate .skill-fill {
  width: 100%;
}


@media (max-width: 768px) {
  .skills-container {
    max-width: 350px;
  }

  .skills-container>h1 {
    font-size: 40px;
  }
}