@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

.contact-container {
  background-image: url('../../resources/background/contact.png');
  background-color: #1b1b1b;
  color: #ffffff;
  padding: 20px;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  font-family: 'Poppins', sans-serif;
  align-items: flex-start;
}

.fromLeft {
  animation: 1s ease-out 0s 1 slideInFromLeft;
}

.fromRight {
  animation: 1s ease-out 0s 1 slideInFromRight;
}

.centerClass {
  text-align: center;
  font-size: 40px;
}

.contactHeader {
  background-color: black;
  padding: 20px;
  border-radius: 20px;
}

.social-iconsC {
  margin-left: auto;
  margin-right: auto;
  max-width: 315px;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  margin-top: 20px;
  background-color: black;
  border-radius: 20px;
  padding: 5px;
}

.social-linkC {
  display: flex;
  align-items: center;
  margin: 10px;
  text-decoration: none;
  color: #ffffff;

}

.social-linkC:hover {
  transform: scale(1.1);
}

.social-iconC {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

@media (max-width: 768px) {
  .contactHeader {
    max-width: 350px;
  }

  .contact-container {
    max-width: 360px;
  }

}